@import url(https://fonts.googleapis.com/earlyaccess/cwtexkai.css);
section.app-cpnt.main {
  position: fixed;
  width: 60%;
  height: 100vh;
  overflow: hidden;
}
section.app-cpnt.main-background {
  position: relative;
  height: 100vh;
  width: 100%;
}

section.app-cpnt.main-background > section.countdown {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  color: white;
  margin: 50% 10% 10%;
}

section.app-cpnt.main-background > section.theme {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

section.app-cpnt.main-background > section.theme.active {
  opacity: 1;
}

section.app-cpnt.main-background .content  {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 10% 20% 25% 10%;
  color: white;
}

section.app-cpnt.main-background .content.show-countdown > .bottom.countdown  {
  display: block;
}

section.app-cpnt.main-background .bottom  {
  position: absolute;
  top: 0;
}

section.app-cpnt.main-background .title {
  font-size: 74px;
  line-height: 70px;
  background: rgba(255,255,255,0.1);
  width: 200px;
}

section.app-cpnt.main-background .title > font {
  background: rgba(97, 97, 97, 0.1);
  display: inline-block;
  height: 120px;
}

section.app-cpnt.main-background .desc {
  margin-top: 20px;
  font-size: 25px;
  white-space: nowrap;
  line-height: 22px;
  background: rgba(126, 126, 126, 0.1)
}
section.app-cpnt.countdown {
  white-space: nowrap;
}

section.app-cpnt.countdown.vertical {
  font-family: 'Sacramento', cursive;
}


section.app-cpnt.countdown.vertical > span:first-child {
  text-align: right;
  margin-right: 20px;
  line-height: 47px;
  font-size: 35px;
}

section.app-cpnt.countdown.vertical > span {
  display: inline-block;
  width: 100px;
  line-height: 45px;
  font-size: 50px;
}
section.app-cpnt.sub > .app-cpnt.welcome {
  position: relative;
  height: 100vh;
  background: rgb(253, 181, 161);
}

section.app-cpnt.sub > .app-cpnt.welcome > .background {
  width: 100%;
  height: 100%;
  font-size: 30px;
  text-align: center;
  color: #444;
}

section.app-cpnt.welcome .content {
  position: absolute;
  margin: 10% 0 20%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
section.app-cpnt.welcome .content .bottom {
  position: absolute;
  top: 60%;
  width: 100%;
}
section.app-cpnt.welcome .city{
  font-size: 40px;
}

section.app-cpnt.welcome .date {
  font-size: 25px;
  margin-bottom: 25px;
}

section.app-cpnt.welcome .location {
  margin: 30px 0 15px;
  font-size: 30px;
}
section.app-cpnt .gallery-puzzle {
  height: 100vh;
}
section.app-cpnt .puzzle {
  width: 90%;
  height: 400px;
  overflow: hidden;
  font-size: 30px;
  margin: 50px auto 0;
}

section.app-cpnt .puzzle > span:first-child {
  width: 30%;
}

section.app-cpnt .puzzle > span {
  display: inline-block;
  height: 200px;
  box-sizing: border-box;
  border: 2px solid;
  font-size: 0;
  padding: 0;
  margin: 0;
  position: relative;
}

section.app-cpnt .puzzle > span:nth-child(2) {
  width: 70%;
}

section.app-cpnt .puzzle > span:nth-child(3) {
  width: 55%;
}

section.app-cpnt .puzzle > span:nth-child(4) {
  width: 45%;
}

section.app-cpnt .puzzle > span > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

section.app-cpnt .puzzle > span > div {
  background: black;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  opacity: 0;
  color: white;
  font-size: 30px;
  text-align: center;
  line-height: 200px;
  cursor: pointer;
}

section.app-cpnt .puzzle > span > div:hover {
  opacity: 0.7;
}
section.app-cpnt.timeline .event {
  width: 100%;
  margin: 0 auto 100px;
  white-space: nowrap;
  position: relative;
}


section.app-cpnt.timeline .event > section {
  width: 250px;
  margin: 50px auto;
}

section.app-cpnt.timeline .event > section.time {
  position: absolute;
  top: 0;
  margin: 0;
}

section.app-cpnt.timeline .divide {
  margin: 30px;
  position: relative;
}

section.app-cpnt.timeline .divide > span {
  display: inline-block;
}

section.app-cpnt.timeline .divide > span:first-child {
  width: 185px;
  white-space: nowrap;
}

section.app-cpnt.timeline .divide > span:last-child {
  width: -webkit-calc(100% - 210px);
  border: 1px solid #777;
  position: absolute;
  right: 10px;
  top: 7px;
}

section.app-cpnt.timeline section.gallery > img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

section.app-cpnt.timeline .title-location {
  margin: 80px auto 20px;
  width: 200px;
  text-align: center;
  font-size: 30px;
}
section.app-cpnt.photo-frame {
  width: 250px;
  height: 250px;
  position: relative;
}

section.app-cpnt.photo-frame > img.frame {
  z-index: 1;
  width: 100%;
  height: 100%;
}

section.app-cpnt.photo-frame > img {
  position: absolute;
  width: 100%;
  height: 100%;
}
section.app-cpnt.schedule {
}
section.app-cpnt.footer > div {
  width: 100%;
  text-align: center;
  font-size: 40px;
  margin: 100px 0;
}
section.app-cpnt.sub {
  position: relative;
  width: 40%;
  height: 100%;
  margin-left: auto;

  color: rgba(255,255,255,.9);
  background: #4c4f52;
}

section.app-cpnt.schedule {
  padding-left: 0;
}

section.app-cpnt.schedule > .title {
  text-align: center;
  font-size: 40px;
  margin: 30px 0 40px;
}
section.app-cpnt.home {
  min-height: 600px;
  font-size: 20px;
}

body,div {
  font-family: 'hsst';
}

font.title-special {
  font-family: 'Sacramento', cursive;
}

section.app-cpnt > .title {
  text-align: center;
  font-size: 40px;
  margin: 30px 0 40px;
}

.app-cpnt .divie {
  width: 100%;
  border-bottom: 2px solid #888;
  margin-bottom: 30px;
}

@font-face {
  font-family: 'hsst';
  src: url(/static/media/hsst.cdf3e0f9.ttf);
}


@media screen and (max-width: 800px) {
  section.app-cpnt.main {
    width: 100%;
    position: static;
  }
  
  section.app-cpnt.sub {
    width: 100%;
  }
}

